.home_wish {

    margin:0;

    padding:0;    

    position:absolute ;

    top:4%;

    width:100%;

    text-align:center;

    font-size:3em;

    color: red;

    letter-spacing: 0.home_1em;

    font-family: 'Pacifico', cursive;

     background: -webkit-linear-gradient(#ffff00, #ff1493);

    -webkit-background-clip: text;

    -webkit-text-fill-color: transparent;

    -webkit-animation: hue 3s infinite ease-in-out;

}



@-webkit-keyframes hue {

    0%{ 

        -webkit-filter: hue-rotate(0deg);

        transform:scale(1) rotate(0deg);

    }

    50%{ -webkit-filter: hue-rotate(360deg);

        transform:scale(1.home_2) rotate(5deg);

    }

}



.home_cake {

    position: absolute;

    width: 250px;

    height: 200px;

    top: 50%;

    left: 50%;

    margin-top: -70px;

    cursor:pointer;

    margin-left: -125px;

}



.home_plate {

    position: absolute;

    width: 290px;

    height: 130px;

    bottom: -10px;

    left: -15px;

    background-color: #ccc;

    border-radius: 25%;

    box-shadow: 0 5px 0 0 #000;

}



.home_cake > * {

    position: absolute;

}



.home_layer {

  position: absolute;

  display: block;

  width: 250px;

  height: 100px;

  border-radius: 25%;

  background-color: #2a0a0a;

  border:3px solid #fff;

  box-shadow:

        0 15px 0 0 #2a0a0a,

        0 30px 0 0 #2a0a0a,

        0 33px 0 0 #fff,

        0 45px 0 0 #2a0a0a,

        0 60px 0 0 #2a0a0a,

        0 63px 0 0 #fff,

        0 75px 0 0 #2a0a0a,

        0 90px 0 0 #2a0a0a;

}



.home_icing {

    top: 3px;

    left: 3px;

    background-color: #fff;

    border:5px dotted #2a0a0a;

    width: 240px;

    height: 90px;

    border-radius: 25%;

}



.home_name {

    margin:0;

    padding:0;    

    position:absolute ;

    top:10%;

    width:100%;

    text-align:center;

    font-size:1em;

    color: #2a0a0a;

